				multiLang: {
			domainName: 'uapbd',
			currentLocale: 'zh-CN',
			moduleId: 'refer_uapbd',
		},

		refType: 'tree',
		refName: 'refer-000269',/* 国际化处理： 岗位序列(所有)*/
		placeholder: 'refer-000269',/* 国际化处理： 岗位序列(所有)*/
